import { MONEY_DEFAULTS, defaultMoneyOptions } from './constants'
import { getNumberOfZeros, cookMoneyString } from './helpers'

export function formatMoney(price, options = defaultMoneyOptions) {
  return cookMoneyString(price, options)
}

export function formatMoneyDiff(
  rawDiff,
  rawPrice,
  options = defaultMoneyOptions,
) {
  const useHundredth = !!(rawPrice < 1 && options.hundredth)
  const price = useHundredth ? rawPrice * 100 : rawPrice
  const numberOfZeros = getNumberOfZeros(price)

  const minCost = Math.pow(10, -(numberOfZeros + (useHundredth ? 2 : 0)))
  const diff = rawDiff ? Math.max(Math.abs(rawDiff), minCost) : rawDiff

  return cookMoneyString(diff, options, {
    useHundredth,
    numberOfZeros,
  })
}

export function createMoneyFormatter(options) {
  return (price, currentOptions) =>
    formatMoney(price, { ...options, ...currentOptions })
}

export function createMoneyDiffFormatter(options) {
  return (diff, price, currentOptions) =>
    formatMoneyDiff(diff, price, { ...options, ...currentOptions })
}

export const formatUSD = createMoneyFormatter(MONEY_DEFAULTS.USD)
export const formatUSDShort = createMoneyFormatter({
  ...MONEY_DEFAULTS.USD,
  short: true,
})
export const formatUSDDiff = createMoneyDiffFormatter(MONEY_DEFAULTS.USD)
