const divider = '~'
const conversionChangeChanelId = '2'
const priceFeedMap = {
  type: {
    [conversionChangeChanelId]: {
      fromSymbol: 0x0, // hex for binary 0, it is a special case of fields that are always there
      toSymbol: 0x0, // hex for binary 0, it is a special case of fields that are always there
      price: 0x1, // hex for binary 1
      openHour: 0x2, // hex for binary 10
      open24Hour: 0x4, // hex for binary 100
      open7Day: 0x8, // hex for binary 1000
    },
  },
}

class FeedMessageParser {
  parse(message) {
    return this._unpack(message, priceFeedMap)
  }

  _parseSafeString = (str) => {
    const safeStr = (str + '').replace(
      new RegExp(divider, 'g'),
      `&#${divider.charCodeAt()};`,
    )

    if (safeStr && safeStr.match) {
      const jsonMatch = safeStr.match(/^JSON:(.*)$/)

      if (jsonMatch && jsonMatch[1]) {
        try {
          return JSON.parse(jsonMatch[1])
        } catch (e) {
          return safeStr
        }
      }
    }

    return safeStr
  }

  _unpack = (str, fieldMaps) => {
    const valuesArray = str.split(divider)
    const valuesArrayLength = valuesArray.length
    const mask = valuesArray[valuesArrayLength - 1]
    const maskInt = parseInt(mask, 16) || 0
    const unpacked = {}
    let path = Object.keys(fieldMaps)
    let currentValueIndex = 0
    let map = fieldMaps

    for (let i = 0; i < path.length; i++) {
      const property = path[i]
      const value = valuesArray[currentValueIndex]

      try {
        if (typeof map[property] === 'object') {
          map = map[property][value]
          path = [...path, ...Object.keys(map)]
          unpacked[property] = this._parseSafeString(value)
          currentValueIndex++
        } else if (map[property] === 0 || maskInt & map[property]) {
          unpacked[property] = this._parseSafeString(value)
          currentValueIndex++
        }
      } catch (e) {
        currentValueIndex++
      }
    }

    return unpacked
  }
}

export default new FeedMessageParser()
