import io from 'socket.io-client'
import ee from 'event-emitter'
import feedMessageParser from './feedMessageParser'

function toArray(...args) {
  switch (args.length) {
    case 0:
      return []
    case 1:
      return Array.isArray(args[0]) ? args[0] : [args[0]]
    default:
      return [].concat(args)
  }
}

class PriceFeedSocket {
  _socketio = undefined
  _subscriptions = new Set()
  _connected = false

  constructor() {
    this._socketio = io('http://app.buysellhodlapp.com/', {
      path: '/socketio',
      reconnection: false,
    })

    this._socketio.on('connect', this._handleConnect)
    this._socketio.on('connect_error', this._handleConnectionError)
    this._socketio.on('disconnect', this._handleDisconnect)
    this._socketio.on('message', this._handleMessage)
  }

  addSubscriptions(subscriptions) {
    subscriptions = toArray(subscriptions)
    const newSubscriptions = subscriptions.filter(
      (s) => !this._subscriptions.has(s),
    )

    if (newSubscriptions.length > 0) {
      newSubscriptions.forEach((s) => this._subscriptions.add(s))

      this.emitSocketEvent('subscribe', { subs: newSubscriptions })
    }
  }

  deleteSubscriptions(subscriptions) {
    const oldSubscriptions = subscriptions.filter((s) =>
      this._subscriptions.has(s),
    )

    if (oldSubscriptions.length > 0) {
      oldSubscriptions.forEach((s) => this._subscriptions.delete(s))

      this.emitSocketEvent('unsubscribe', { subs: oldSubscriptions })
    }
  }

  emitSocketEvent(eventName, data) {
    this._socketio.emit(
      eventName,
      typeof data === 'string' ? data : JSON.stringify(data),
    )
  }

  _handleConnect = () => {
    this.emit('connect')

    if (this._subscriptions.size !== 0) {
      this.emitSocketEvent('subscribe', { subs: [...this._subscriptions] })
    }
  }

  _handleConnectionError = () => {
    this.emit('connect_error')
    this._socketio.connect()
  }

  _handleDisconnect = () => {
    this.emit('disconnect')
    this._socketio.connect()
  }

  _handleMessage = (message) => {
    const parsedMessage = feedMessageParser.parse(message)

    this.emit('message', parsedMessage)
  }
}

export default ee(new PriceFeedSocket())
