import { h } from 'preact'
import PropTypes from 'prop-types'

const Bar = ({ percentage, barClass, barColor, wide, noMargin }) => (
  <div
    className={'flex bar ' + (wide ? 'wide' : '') + (noMargin && ' no-margin')}
  >
    <div
      className={barClass}
      style={{
        width: `${percentage}%`,
        backgroundColor: barColor,
      }}
    />
  </div>
)

Bar.propTypes = {
  percentage: PropTypes.number,
}

export default Bar
