import { h, Component } from 'preact'
import priceChangeEventEmitter from './../../../pricing/priceChangeEventEmitter'
import { formatUSD } from './../../../services/moneyService'
import ColoredCell from './coloredCell'

const _getForecastValue = (targetPrice, currentPrice) => {
  const forecastValue = (targetPrice / currentPrice - 1) * 100
  const finalValue = isFinite(forecastValue) ? forecastValue : 0
  return {
    forecast: finalValue.toFixed(0),
    absoluteForecast: Math.abs(finalValue).toFixed(0),
  }
}

export default class TableRow extends Component {
  state = { currentPrice: 0 }

  componentDidMount() {
    if (this.props.price) {
      this.setState((state) => ({ ...state, currentPrice: this.props.price }))
    }

    if (this.props.coinName) {
      priceChangeEventEmitter.subscribeOnPriceChange(
        this.props.coinName,
        'USD',
        this._onPriceChange,
      )
    }
  }

  _onPriceChange = ({ fromSymbol, toSymbol, price }) => {
    this.setState((state) => ({ ...state, currentPrice: price }))
  }

  render({
    isEven,
    rank,
    image,
    fullName,
    rating,
    buy,
    hodl,
    sell,
    priceTarget,
  }) {
    const formatedPrice = formatUSD(this.state.currentPrice)
    const formatedPriceTarget = formatUSD(priceTarget)
    const projection = _getForecastValue(priceTarget, this.state.currentPrice)

    return (
      <div className={`flex table-row ${isEven ? 'dark' : 'light'}`}>
        <div className="row-group name">
          <span className="order">#{rank}</span>
          <img src={image} />
          <span className="coin-name">{fullName}</span>
        </div>
        <div className="row-group rating">
          <span className={`${rating} rounded`}>{rating.toUpperCase()}</span>
        </div>
        <div className="row-group bsh">
          <span className={rating === 'buy' && 'green'}>{buy}%</span>
          <span className={rating === 'hodl' && 'blue'}>{hodl}%</span>
          <span className={rating === 'sell' && 'red'}>{sell}%</span>
        </div>
        <div className="row-group price">
          <ColoredCell value={this.state.currentPrice} text={formatedPrice} />
        </div>
        <div className="row-group target">
          <span>{formatedPriceTarget}</span>
        </div>
        <div className="row-group projection">
          <span className={projection.forecast > 0 ? 'green' : 'red'}>
            {projection.forecast > 0 ? (
              <span className="triangle">&#9650; </span>
            ) : (
              <span className="triangle">&#9660; </span>
            )}
            {Math.abs(projection.forecast)}%
          </span>
        </div>
      </div>
    )
  }
}
