import ee from 'event-emitter'
import priceFeedSocket from './priceFeedSocket'

class PriceChangeEventEmitter {
  constructor() {
    this._callbacks = {}

    priceFeedSocket.on('message', this._onFeedMessage)
  }

  subscribeOnPriceChange(fromSymbol, toSymbol, callback) {
    const key = fromSymbol + '~' + toSymbol

    if (this._callbacks[key]) {
      if (!this._callbacks[key].callback.find((x) => x === callback)) {
        this._callbacks[key].callbacks.push(callback)
      }
    } else {
      this._callbacks[key] = {
        fromSymbol,
        toSymbol,
        callbacks: [callback],
      }

      priceFeedSocket.addSubscriptions(['2~' + key])
    }
  }

  _onFeedMessage = (message) => {
    if (message.type === '2' && message.price) {
      const fromSymbol = message.fromSymbol
      const toSymbol = message.toSymbol
      const key = fromSymbol + '~' + toSymbol

      if (this._callbacks[key]) {
        this._callbacks[key].callbacks.forEach((c) => c(message))
      }
    }
  }
}

export default ee(new PriceChangeEventEmitter())
