import { h, Component } from 'preact'
import PropTypes from 'prop-types'
import './colored-cell.scss'

export default class ColoredCell extends Component {
  state = { classname: 'colored-cell' }

  componentDidUpdate(prevProps) {
    if (
      prevProps.value &&
      this.props.value &&
      prevProps.value !== this.props.value
    ) {
      this.startAnimation(this.props.value - prevProps.value)
    }
  }

  startAnimation(diff) {
    clearTimeout(this._timer)

    this.setState(
      (state) => ({ ...state, classname: 'colored-cell' }),
      () => {
        this._timer = setTimeout(() => {
          this.setState((state) => ({
            ...state,
            classname: diff < 0 ? 'colored-cell greenb' : 'colored-cell redb',
          }))
        }, 10)
      },
    )
  }

  render() {
    return (
      <span className={this.state.classname}>
        {this.props.text || this.props.value}
      </span>
    )
  }
}

ColoredCell.propTypes = {
  value: PropTypes.number.isRequired,
  text: PropTypes.string,
}
