import numeral from 'numeral'

export function cookMoneyString(rawValue, moneyOptions, options = {}) {
  const useHundredth = isBool(options.useHundredth)
    ? options.useHundredth
    : !!(rawValue < 1 && rawValue !== 0 && moneyOptions.hundredth)
  const value = useHundredth ? rawValue * 100 : rawValue
  const numberOfZeros = options.numberOfZeros || getNumberOfZeros(value)

  const { hideSymbolPrefix, short } = moneyOptions
  const { prefix, postfix = '' } = useHundredth
    ? moneyOptions.hundredth
    : moneyOptions

  const pattern = makeMoneyPattern({
    numberOfZeros,
    prefix,
    hideSymbolPrefix,
    short,
  })

  // DEVNOTE: 'numeral' doesn't support postfix that is why it is added manually
  return `${numeral(value).format(pattern)}${postfix}`
}

export function zerosString(count) {
  return Array.from(new Array(count))
    .map(() => 0)
    .join('')
}

export function getNumberOfZeros(price) {
  switch (true) {
    case price === 0:
    case price > 1:
      return 2
    case price > 0.1:
      return 4
    case price > 0.01:
      return 5
    default:
      return 6
  }
}

export function makeMoneyPattern({
  numberOfZeros,
  prefix = '',
  hideSymbolPrefix,
  short = false,
}) {
  return `${hideSymbolPrefix ? '' : prefix}0,0.${short ? '[' : ''}${zerosString(
    numberOfZeros,
  )}${short ? ']' : ''}`
}

export function isBool(item) {
  return typeof item === 'boolean'
}
